import { createRouter, createWebHashHistory } from 'vue-router'
import index from '../views'
import home from '../views/pages/home.vue'
import login from '../views/pages/login.vue'
import xueshengList from '@/views/pages/xuesheng/list'
import xueshengDetail from '@/views/pages/xuesheng/formModel'
import xueshengAdd from '@/views/pages/xuesheng/formAdd'
import xueshengRegister from '@/views/pages/xuesheng/register'
import xueshengCenter from '@/views/pages/xuesheng/center'
import jiaoshiList from '@/views/pages/jiaoshi/list'
import jiaoshiDetail from '@/views/pages/jiaoshi/formModel'
import jiaoshiAdd from '@/views/pages/jiaoshi/formAdd'
import xiaoduanjibenxinxiList from '@/views/pages/xiaoduanjibenxinxi/list'
import xiaoduanjibenxinxiDetail from '@/views/pages/xiaoduanjibenxinxi/formModel'
import xiaoduanjibenxinxiAdd from '@/views/pages/xiaoduanjibenxinxi/formAdd'
import nianjiList from '@/views/pages/nianji/list'
import nianjiDetail from '@/views/pages/nianji/formModel'
import nianjiAdd from '@/views/pages/nianji/formAdd'
import kechengxinxiList from '@/views/pages/kechengxinxi/list'
import kechengxinxiDetail from '@/views/pages/kechengxinxi/formModel'
import kechengxinxiAdd from '@/views/pages/kechengxinxi/formAdd'
import kejianziyuanList from '@/views/pages/kejianziyuan/list'
import kejianziyuanDetail from '@/views/pages/kejianziyuan/formModel'
import kejianziyuanAdd from '@/views/pages/kejianziyuan/formAdd'
import kechengzuoyeList from '@/views/pages/kechengzuoye/list'
import kechengzuoyeDetail from '@/views/pages/kechengzuoye/formModel'
import kechengzuoyeAdd from '@/views/pages/kechengzuoye/formAdd'
import zuoyejinduList from '@/views/pages/zuoyejindu/list'
import zuoyejinduDetail from '@/views/pages/zuoyejindu/formModel'
import zuoyejinduAdd from '@/views/pages/zuoyejindu/formAdd'
import storeupList from '@/views/pages/storeup/list'

const routes = [{
		path: '/',
		redirect: '/index/home'
	},
	{
		path: '/index',
		component: index,
		children: [{
			path: 'home',
			component: home
		}
		, {
			path: 'xueshengList',
			component: xueshengList
		}, {
			path: 'xueshengDetail',
			component: xueshengDetail
		}, {
			path: 'xueshengAdd',
			component: xueshengAdd
		}
		, {
			path: 'xueshengCenter',
			component: xueshengCenter
		}
		, {
			path: 'jiaoshiList',
			component: jiaoshiList
		}, {
			path: 'jiaoshiDetail',
			component: jiaoshiDetail
		}, {
			path: 'jiaoshiAdd',
			component: jiaoshiAdd
		}
		, {
			path: 'xiaoduanjibenxinxiList',
			component: xiaoduanjibenxinxiList
		}, {
			path: 'xiaoduanjibenxinxiDetail',
			component: xiaoduanjibenxinxiDetail
		}, {
			path: 'xiaoduanjibenxinxiAdd',
			component: xiaoduanjibenxinxiAdd
		}
		, {
			path: 'nianjiList',
			component: nianjiList
		}, {
			path: 'nianjiDetail',
			component: nianjiDetail
		}, {
			path: 'nianjiAdd',
			component: nianjiAdd
		}
		, {
			path: 'kechengxinxiList',
			component: kechengxinxiList
		}, {
			path: 'kechengxinxiDetail',
			component: kechengxinxiDetail
		}, {
			path: 'kechengxinxiAdd',
			component: kechengxinxiAdd
		}
		, {
			path: 'kejianziyuanList',
			component: kejianziyuanList
		}, {
			path: 'kejianziyuanDetail',
			component: kejianziyuanDetail
		}, {
			path: 'kejianziyuanAdd',
			component: kejianziyuanAdd
		}
		, {
			path: 'kechengzuoyeList',
			component: kechengzuoyeList
		}, {
			path: 'kechengzuoyeDetail',
			component: kechengzuoyeDetail
		}, {
			path: 'kechengzuoyeAdd',
			component: kechengzuoyeAdd
		}
		, {
			path: 'zuoyejinduList',
			component: zuoyejinduList
		}, {
			path: 'zuoyejinduDetail',
			component: zuoyejinduDetail
		}, {
			path: 'zuoyejinduAdd',
			component: zuoyejinduAdd
		}
		, {
			path: 'storeupList',
			component: storeupList
		}
		]
	},
	{
		path: '/login',
		component: login
	}
	,{
		path: '/xueshengRegister',
		component: xueshengRegister
	}
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
